body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Mainframe {
    background-color: #474B4F;
    width: 100%;
    height: 100%;
    color: white;
    display: grid;
    grid-template-columns: 25vw 50vw 25vw;
    grid-template-rows:80px 90%;
   
    
     
 }

 .Resume{
    background-color: #222629;
    color: white;
    justify-content: center;
   
    text-align: center;
    grid-row-start: 1;
    grid-row-end: 1;
    grid-column-start:1;
    grid-column-end:4;
    
 }


 .lists{
    padding: 10px;
    cursor: pointer;
    border-left: solid;
    border-color: yellow;
    
   
    
 }

 
.Headings{

    
    grid-row-start: 2;
    grid-row-end: 2;
    grid-column-start:1;
    grid-column-end:1;
    justify-content: center;
    margin-top: 80px;
    padding-left: 50px;
}
.Description{

   
    grid-row-start: 2;
    grid-row-end: 2;
    grid-column-start:2;
    grid-column-end:2;
    padding-top: 80px;
    
}

.Images{
    background-color: w;
   
    
    grid-row-start: 2;
    grid-row-end: 2;
    grid-column-start:3;
    grid-column-end:3;
   margin-top: 80px;
  
}
#resume{
    padding-top: 10px;
}
